<template>
  <tal-notification-page @on-token-error="getNoticeToken"></tal-notification-page>
</template>

<script>
import { noticePlatLogin } from '@/api/bd'
export default {
  name: 'noticeCenter',
  methods: {
    getNoticeToken () {
      noticePlatLogin().then(data => {
        sessionStorage.setItem('X-Ca-Notice-Token', data.token)
        this.$store.commit('setNoticeToken', data.token)
      }).catch(err => {
        this.$Message.error({
          content: '通知平台登录失败：' + err.message
        })
      })
    }
  }
}
</script>
